import React from "react";

const courses = [
  {
    title: "MS-CIT",
    duration: "3 Months",
    img: require("../assets/courses/mscit.png"),
    points: [
      "Fundamental computer skills",
      "Internet & Email usage",
      "Office tools & digital literacy",
    ],
  },
  {
    title: "Advanced Excel",
    duration: "2 Months",
    img: require("../assets/courses/excel.png"),
    points: [
      "Custom formats & validation",
      "Pivot Tables & Charts",
      "HLOOKUP, VLOOKUP, and data analysis",
    ],
  },
  {
    title: "Tally Prime with GST",
    duration: "3 Months",
    img: require("../assets/courses/tally.png"),
    points: [
      "Trading/Manufacturing accounting",
      "Display & Reporting tools",
      "GST entries and filing",
    ],
  },
  {
    title: "Diploma in Financial Accounting",
    duration: "6 Months",
    img: require("../assets/courses/dfa.jpg"),
    points: [
      "Office Assistance & Advanced Excel",
      "Tally + GST practicals",
      "Banking & Insurance fundamentals",
    ],
  },
  {
    title: "Share Market",
    duration: "2 Months",
    img: require("../assets/courses/sharemarket.png"),
    points: [
      "BSE/NSE basics & investment types",
      "Chart patterns & TradingView",
      "Strategies: SIP & Mutual Funds",
    ],
  },
  {
    title: "Work From Home Tools",
    duration: "1 Month",
    img: require("../assets/courses/remote.png"),
    points: [
      "PAN card & document services",
      "e-Seva center workflows",
      "Affidavit & online applications",
    ],
  },
];

const CourseCard = ({ title, img, duration, points }) => (
  <article className="rounded-2xl overflow-hidden border border-slate-200 bg-white shadow-sm hover:shadow-md transition-all duration-300 flex flex-col">
    <img
      src={img}
      alt={title}
      className="h-52 w-full object-cover"
      loading="lazy"
    />
    <div className="p-5 flex-1 flex flex-col justify-between">
      <div>
        <h3 className="text-lg font-semibold text-[#DC2626]">{title}</h3>
        <p className="text-sm text-slate-500 mb-2">Duration: {duration}</p>
        <ul className="list-disc pl-5 text-slate-700 space-y-1">
          {points.map((p, i) => (
            <li key={i}>{p}</li>
          ))}
        </ul>
      </div>
    </div>
  </article>
);

export default function CoursesPage() {
  return (
    <main className="py-20 bg-slate-50 text-slate-900">
      <div className="mx-auto max-w-6xl px-4">
        {/* Header */}
        <header className="mb-10 text-center">
          <h1 className="text-4xl font-bold text-[#DC2626] mb-2">Our Courses</h1>
          <p className="text-slate-600 text-lg">
            Career-oriented programs designed by industry trainers to help you
            build real-world skills.
          </p>
        </header>

        {/* Course Cards */}
        <div className="grid gap-8 sm:grid-cols-2 lg:grid-cols-3">
          {courses.map((c, i) => (
            <CourseCard key={i} {...c} />
          ))}
        </div>

        {/* Counselling Section */}
        <div className="mt-16 rounded-2xl border border-slate-200 bg-white p-8 shadow-sm text-center">
          <h2 className="text-2xl font-semibold text-[#1E3A8A]">
            Need Counselling?
          </h2>
          <p className="mt-2 text-slate-600">
            Message us on WhatsApp  our team will help you choose the perfect
            course for your career goals.
          </p>
          <a
            href="https://wa.me/919552345061?text=Hello%20IT%20Professional%20Lab!%20I%E2%80%99m%20interested%20in%20your%20courses."
            target="_blank"
            rel="noreferrer"
            className="mt-5 inline-block rounded-xl bg-green-600 px-6 py-3 font-semibold text-white shadow hover:brightness-110 transition-all"
          >
            WhatsApp Us
          </a>
        </div>
      </div>
    </main>
  );
}
